import React, { useState, useEffect} from 'react'
import AddPlayer from './components/AddPlayer'
import DisplayPlayers from './components/DisplayPlayers'
import EditPlayer from './components/EditPlayer'
import Services from './services/players'

const App = () => {
  const [editing, setEditing] = useState(false)
  const [playerToEdit, setPlayerToEdit] = useState()
  const [players, setPlayers] = useState([])

  const addPlayer = obj => {
    Services.createService(obj).then(data => setPlayers(players.concat(data)))
  }

  const deletePlayer = id => {
    const newArray = [...players]
    Services.deleteService(id).then(data => setPlayers(newArray.filter(p => p.id !== id)))
  }

  const whichPlayer = obj => {
    setEditing(true);
    setPlayerToEdit(obj);
  };

  const editPlayer = (id, obj) => {
    const newArray = [...players]
    Services.editService(obj, id).then(data => {
      setPlayers(newArray.map(p => p.id === id ? data : p))
      setEditing(false)
    })
  };

  const toggle = (id, obj) => {
    const newArray = [...players]
    Services.toggleService(id, obj).then(data => {
      setPlayers(newArray.map(p => p.id === id ? data: p))
    })
  };

  useEffect(() => {
    Services.getService().then(data => setPlayers(data))
  }, [])

  return (
    <div className="center">
      <h1>Practicing</h1>
      <hr />

      {editing ?
        <EditPlayer
          setEditing={setEditing}
          playerToEdit={playerToEdit}
          editPlayer={editPlayer}
        /> :
        <AddPlayer
          addPlayer={addPlayer}
        />}

      <DisplayPlayers
        players={players}
        deletePlayer={deletePlayer}
        whichPlayer={whichPlayer}
        toggle={toggle}
      />
    </div>
  )
}

export default App
